import MyActionsheet from '@/components/actionsheet2.vue'
export default {
  components: {
    MyActionsheet
  },
  data () {
    return {
      actionsheetObj: {
        isActionSheetShow: false,
        menus: { online: '上线计划', offline: '非上线计划' },
        urls: { online: '/newOnlinePlan', offline: '/newOfflinePlan' }
      },
      buttons: [
        {
          type: 'primary',
          text: '重新编辑',
          link: '/report'
        },
        {
          type: 'primary',
          text: '新计划',
          onClick: () => {
            this.actionsheetObj.isActionSheetShow = true
          }
        },
        {
          type: 'default',
          text: '返回工作台',
          link: '/'
        }
      ]
    }
  }
}



